hr {
	width: 100%;
	border: 1px solid black;
}

.Post {
	width: 100vw;
	max-width: 900px;
	text-align: start;
}

.commentsVotes {
	margin-left: 10px;
	text-align: center;
}

a {
	font-size: 20px;
	color: var(--quaternary-bg-color);
	text-decoration: none;
}

a:hover {
	color: var(--secondary-bg-color);
}

.moreBtn {
	position: absolute;
	bottom: -3px;
	height: 17px;
	padding: 0;
	width: 100%;
	left: 0;
	border-radius: 0px 0px 5px 5px;
}

.shortBrief,
.postLink {
	max-width: 700px;
	width: 70vw;
	word-wrap: break-word;
}
