@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@1,300&family=Roboto:ital,wght@0,300;0,500);
html {
	overflow-y: hidden;
	overflow-x: hidden;
}

body {
	--primary-bg-color: hsl(159, 56%, 7%);
	--secondary-bg-color: hsl(35, 32%, 88%);
	--tertiary-bg-color: hsl(155, 43%, 18%);
	--quaternary-bg-color: hsl(43, 30%, 60%);

	--btmPrimary-bg-color: hsl(43, 30%, 60%);
	--btmSecondary-bg-color: hsl(43, 30%, 79%);
	--btmTertiary-bg-color: hsl(194, 43%, 18%);

	--cardPrimary-bg-color: hsl(190, 100%, 10%);

	--error-color: hsl(0, 77%, 42%);

	margin: 0;
	font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
		'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background-color: var(--secondary-bg-color);
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.header {
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	padding: 0 30px 0 30px;
	min-height: 7vh;
	background-color: var(--primary-bg-color);
	z-index: 4;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.header h1 {
	font-size: 30px;
	font-family: 'Roboto';
	color: var(--secondary-bg-color);
	margin: 0;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
	position: relative;
	background-color: var(--primary-bg-color);
	text-shadow: 0.5px 0.5px 1px rgb(110, 110, 110), 1px 1px 1px rgb(94, 94, 94),
		2px 2px 1px rgb(82, 82, 82), 3px 3px 0.5px rgb(70, 70, 70), 4px 4px 0.5px rgb(48, 48, 48);
}

.menuBtn {
	color: var(--secondary-bg-color);
	border-radius: 5px;
}

.menuBtn:hover {
	background-color: var(--btmPrimary-bg-color);
	transition-duration: 0.4s;
}

/* @media (min-width: 600px) {
	.header {
		min-height: 64px;
	}
} */

.container {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 3;
	width: 100vw;
	height: 100vh;
	background-color: hsla(0, 0%, 0%, 0.65);
	display: flex;
	flex-direction: column;
	margin-right: 10px;
	transition: background-color 0.3s linear;
}

.sideMenuIcon {
	width: 30px;
	height: 30px;
	background-color: inherit;
	display: flex;
	justify-content: center;
	flex-direction: column;
	cursor: pointer;
}

.sideMenuIcon p {
	width: 20px;
	height: 3px;
	background-color: var(--secondary-bg-color);
	margin: 2px 0 2px 4px;
}

.container.closed {
	background-color: transparent;
	pointer-events: none;
}

.container.open {
	background-color: hsla(0, 0%, 0%, 0.65);
}

.navContainer {
	padding: 5px;
	width: 170px;
	height: 100vh;
	background-color: var(--secondary-bg-color);
	z-index: 5;
	text-align: left;
	box-shadow: 0px 5px 10px;
}

.navContainer.closed {
	margin-left: -120%;
	transition: all 1.2s;
}

.navContainer.open {
	margin-left: 0%;
	transition: all 0.3s;
}

.navBarList {
	display: flex;
	flex-direction: column;
}

.navContainer a {
	color: var(--cardPrimary-bg-color);
	padding: 5px;
	border-radius: 5px;
	transition-duration: 0.2s;
	display: flex;
}

.navContainer a:hover {
	background-color: var(--quaternary-bg-color);
}

.App {
	font-family: Roboto;
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		-webkit-animation: App-logo-spin infinite 20s linear;
		        animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.card {
	border-radius: 5px;
	padding: 10px;
	margin: 10px;
	box-shadow: 0px 2px 4px;
	background-color: var(--cardPrimary-bg-color);
}

form {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

p,
h1,
h2,
h3,
h4,
h5 {
	color: var(--secondary-bg-color);
}

textarea,
input {
	border-radius: 5px;
}

button {
	height: 30px;
	border: 0px;
	background-color: var(--btmPrimary-bg-color);
	color: var(--tertiary-bg-color);
	border-radius: 5px;
	cursor: pointer;
	transition: 0.2s;
}

button:hover {
	background-color: var(--btmSecondary-bg-color);
}

::-webkit-scrollbar{
	background-color: inherit;
	width: 12px;
}
::-webkit-scrollbar-thumb{
	background-color: var(--tertiary-bg-color);
	border-radius: 5px;
}

.homeContent {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	align-items: center;
}

.fitText {
	max-width: 700px;
	word-wrap: break-word;
}

hr {
	width: 100%;
	border: 1px solid black;
}

.Post {
	width: 100vw;
	max-width: 900px;
	text-align: start;
}

.commentsVotes {
	margin-left: 10px;
	text-align: center;
}

a {
	font-size: 20px;
	color: var(--quaternary-bg-color);
	text-decoration: none;
}

a:hover {
	color: var(--secondary-bg-color);
}

.moreBtn {
	position: absolute;
	bottom: -3px;
	height: 17px;
	padding: 0;
	width: 100%;
	left: 0;
	border-radius: 0px 0px 5px 5px;
}

.shortBrief,
.postLink {
	max-width: 700px;
	width: 70vw;
	word-wrap: break-word;
}

@-webkit-keyframes rotation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes rotation {
	0% {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

.img {
	height: 100px;
	width: 100px;
}

.Loading {
	display: flex;
	justify-content: center;
	align-items: center;
	-webkit-animation: rotation 1s infinite linear;
	        animation: rotation 1s infinite linear;
	height: 100px;
	width: 100px;
}


.tagSearch {
	margin: 10px;
	padding: 1px;
	border-radius: 5px;
	background-color: var(--btmTertiary-bg-color);
	box-shadow: 0px 0px 4px;
	width: 100vw;
	max-width: 900px;
}
.tagsList {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
}

.searchButton {
	height: 20px;
	width: 100px;
	background-color: var(--cardPrimary-bg-color);
	color: var(--btmPrimary-bg-color);
	box-shadow: black 2px 2px 2px;
	margin-bottom: 8px;
}

.searchButton:hover {
	background-color: hsl(190deg 99% 16%);
}

.tagSearchButton {
	margin: 5px;
}

.choose {
	background-color: var(--btmSecondary-bg-color);
}

.inputContainer {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	position: relative;
	margin-bottom: 15px;
}

.inputContent {
	width: -webkit-fill-available;
	height: 100%;
	resize: none;
	z-index: 2;
	border: 1px solid var(--quaternary-bg-color);
	outline: none;
	background-color: var(--secondary-bg-color);
	transition: color 0.3s;
}

.inputContent.error {
	border: 2px solid var(--error-color);
}

.errorText {
	pointer-events: none;
	position: absolute;
	z-index: 1;
	text-align: left;
	transition: all 0.3s;
}

.Login {
	display: flex;
	justify-content: center;
}

.Register {
	display: flex;
	justify-content: center;
}

.Profile {
	display: flex;
	justify-content: center;
}

.postForm {
	display: flex;
	justify-content: center;
	height: 93vh;
	overflow-y: auto;
}

label {
	color: var(--secondary-bg-color);
	padding: 5px 0 5px 0;
}

.tagList {
	display: flex;
	min-height: 40px;
	overflow: hidden;
	width: -webkit-fill-available;
	justify-content: center;
	align-items: center;
	max-width: 70vw;
	flex-wrap: wrap;
	align-self: center;
}

.selectDiv {
	align-self: center;
	display: flex;
}

.tagsContainer {
	width: -webkit-fill-available;
	position: relative;
	display: flex;
	flex-direction: column;
	padding: 10px 0 10px 0;
}

.postForm label{
	margin-left: 10px;
}

.chip {
	display: flex;
	background-color: var(--btmSecondary-bg-color);
	border-radius: 15px;
	margin: 5px 5px 0 0;
	align-items: center;
	padding: 5px;

}

.chip p {
	margin: 2px;
	pointer-events: none;
	color: var(--btmTertiary-bg-color);
}

.chip.error {
	display: none;
}

.deleteChip {
	width: 25px;
	height: 25px;
	display: flex;
	justify-content: center;
	align-items: center;
	border-radius: 50%;
	background-color: inherit;
	margin-left: 4px;
	color: var(--btmTertiary-bg-color);
}

.deleteChip:hover {
	background-color: var(--secondary-bg-color);
	cursor: pointer;
}

.select {
	position: relative;
}

.select input {
	padding: 0 0 0 10px;
	background-color: inherit;
	outline: none;
	border: 1px solid var(--secondary-bg-color);
	height: 40px;
	width: 200px;
}

.select label {
	height: 18px;
	background-color: var(--cardPrimary-bg-color);
	position: absolute;
	top: 6px;
	left: 10px;
	transition: all 0.2s;
}

.select input:focus ~ label,
.select label.up {
    padding: 0 2px 0px 1px;
    top: -5px;
    height: 10px;
    font-size: 11px;
    color: var(--btmSecondary-bg-color);
    pointer-events: none;
}

.select:hover input {
	border: 1px solid var(--btmPrimary-bg-color);
}

.select input:focus {
	border: 1px solid var(--btmSecondary-bg-color);
}

.select button {
	position: absolute;
	top: 11px;
	right: 10px;
	border-radius: 50%;
	width: 25px;
	height: 25px;
	background-color: inherit;
}

.select button:hover {
	background-color: hsl(213, 25%, 33%);
}

.select button:hover ~ input {
	border: 1px solid var(--btmPrimary-bg-color);
}

.arrow {
	border: 1px solid var(--btmSecondary-bg-color);
	border-width: 0 3px 3px 0;
	display: inline-block;
	padding: 3px;
}

.arrow.down {
	transform: rotate(45deg);
	-webkit-transform: rotate(45deg);
}

.arrow.up {
	transform: rotate(-135deg);
	-webkit-transform: rotate(-135deg);
}

.select:hover .arrow {
	border: 1px solid var(--btmPrimary-bg-color);
	border-width: 0 3px 3px 0;
}

.options {
	position: absolute;
	border: 1px solid var(--primary-bg-color);
	background-color: var(--secondary-bg-color);
	margin-left: 5px;
	width: 190px;
	max-height: 195px;
	border-radius: 0px 0px 10px 10px;
	padding: 5px;
	display: none;
	overflow: auto;
	z-index: 3;
}

.options.open {
	display: block;
}

.select ::-webkit-scrollbar-track{
	margin: 0 0 5px 0;
}

.select ::-webkit-scrollbar {

	width: 5px;
	border-radius: 10px;
	background-color: transparent;
}

.option {
	color: var(--primary-bg-color);
	text-align: left;
	margin: 0;
	padding: 10px;
	border-radius: 5px;
	cursor: pointer;
}

.option:hover {
	background-color: hsl(0, 0%, 84%);
}

.select.error input,
.select.error button:hover ~ input {
	border: 2px solid var(--error-color);
}

.select.error label,
.select.error input:focus ~ label,
.select.error button {
	color: var(--error-color);
}

.postView {
	display: flex;
	justify-content: center;
	overflow: hidden;
}

.fullPost {
	width: 100vw;
	max-width: 1000px;
}

.lowerPost {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.postContent {
	display: flex;
	text-align: left;
	margin-left: 20px;
}

.postText {
	word-wrap:break-word;
	max-width: 900px;
	width: 90vw;
	margin: 0 auto 0 0;
	min-height: 200px;
}

.loadComments {
	margin-bottom: 10px;
}

.briefP {
	white-space: normal;
}

.Vote {
	display: flex;
	flex-direction: column;
	justify-content: center;
	background-color: var(--secondary-bg-color);
	border-radius: 5px;
	padding: 5px;
}

.commentEditor {
	width: 74%;
	height: 100px;
	resize: none;
	margin-right: 10px;
	background-color: var(--secondary-bg-color);
	border-radius: 5px;
}

.commentForm {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	
}

.commentBtn {
	margin-left: 10px;
}

@media screen and (max-width: 600px) {
	.commentForm {
		flex-wrap: wrap;
		flex-direction: row;
		align-content: center;
		justify-content: space-around;
	}
}

.cardProfile {
	height: 70px;
	width: 160px;
	min-width: 125px;
	display: flex;
	align-items: center;
	justify-content: space-around;
	padding: 5px;
	margin: 5px;
}
.lastSeen {
	margin-top: 0;
	font-size: 12px;
}
.name {
	margin-bottom: 5px;
}

.iconProfile {
	top: auto;
	float: left;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 100%;
	min-width: 50px;
	min-height: 50px;
	max-height: 50px;
	font-size: 25px;
	font-weight: bold;
	pointer-events: none;
}

.acronyms {
	-webkit-filter: invert(100%);
	        filter: invert(100%);
	-webkit-touch-callout: none; /* iOS Safari */
	-webkit-user-select: none; /* Safari */ /* Konqueror HTML */ /* Old versions of Firefox */
	-ms-user-select: none; /* Internet Explorer/Edge */
	user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

.commentContent {
	display: flex;
}
.commentText {
	margin: 0 20px 0 5px;
	text-align: left;
	width: 97%;
	display: flex;
	justify-content: space-between;
	flex-direction: column;
	padding-top: 10px;
}

.date {
	font-size: 12px;
	text-align: right;
}

.editing {
	width: 93%;
	height: 100px;
	resize: none;
	background-color: var(--secondary-bg-color);
	margin-right: 5px;
}

.editContainer {
	margin: 0 20px 0 5px;
	text-align: left;
	width: 97%;
	display: flex;
}

.editBtn {
	margin: 0px 0px 0px 10px;
}

.commentText .content {
	margin: 0px 0px 5px 5px;
	background: var(--btmTertiary-bg-color);
	min-height: 70px;
	padding: 5px;
	border-radius: 5px;
}

.editBtnsDiv{
	margin: 0px 0px 0px auto;
}
.report {
	width: 40vw;
	height: 260px;
	position: fixed;
	left: 50%;
	-webkit-transform: translate(-50%);
	        transform: translate(-50%);
	transition: all 0.3s;
	z-index: 10;
	padding: 0px 20px 5px 15px;
	box-shadow: 0px 5px 15px;
}

.report.open {
	bottom: 0vh;
}

.report.close {
	bottom: -50%;
}

.report textarea {
	margin-top: 5px;
	width: 100%;
	min-height: 43%;
	max-height: 100%;
	resize: none;
}

.report .submit {
	margin-top: 5px;
	position: relative;
	bottom: 0;
	min-height: 30px;
}
.report h1 {
	margin: 10px 0 10px 0;
}

.report select {
	border-radius: 5px;
	background-color: var(--btmTertiary-bg-color);
	font-size: 15px;
	color: var(--btmPrimary-bg-color);
}

.reportBackGround {
	width: 100vw;
	height: 100vh;
	position: fixed;
	top: 0;
	left: 0;
	background-color: transparent;
}

.closeReportBtnDiv {
	width: 100%;
	position: relative;
	height: 0;
}

.closeReportBtn {
	height: 25px;
	float: right;
	margin: 10px -10px 0px 0px;
}

@media (max-width:600px) {
	.report {
		width: 80vw;
		max-height: 70vh;
	}
}

.toolTip {
	position: relative;
	z-index: 2;
}
.menu {
	left: -90px;
	top: -10px;
	position: absolute;
	width: 80px;
	background-color: var(--btmSecondary-bg-color);
	padding: 5px 0 5px 0;
	border: 1px solid var(--btmPrimary-bg-color);
}
.menu button {
	padding: 0 0 0 8px;
	background-color: inherit;
	width: 80px;
	text-align: left;
}
.menu button:hover {
	background-color: var(--btmPrimary-bg-color);
}
.login {
	text-align: center;
}
.dots {
	transition: all 0.5s;
	left: -17px;
	position: absolute;
	color: var(--secondary-bg-color);
	width: 20px;
	display: flex;
	justify-content: flex-end;
	border-radius: 25%;
}
.dots:hover {
	background-color: var(--btmSecondary-bg-color);
}
.dots::after {
	content: '\2807';
	font-size: 20px;
}


.tag {
    display: flex;
    border: 0px;
    background-color: var(--btmPrimary-bg-color);
    color: var(--tertiary-bg-color);
    border-radius: 5px;
    cursor: pointer;
    transition: 0.2s;
    font-size: 13px;
    padding: 7px;
    margin: 5px;
    justify-content: center;
    flex-direction: column;
}

.tag:hover {
	background-color: var(--btmSecondary-bg-color);
	color: var(--tertiary-bg-color);
}

.tags {
	margin: 10px;
    padding: 1px;
	border-radius: 5px;
	background-color: var(--btmTertiary-bg-color);
	box-shadow: 0px 0px 4px;
}
.tagsList {
	margin: 5px;
	display: flex;
	justify-content: center;
    flex-wrap: wrap;
}

